.row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.col-lg-8 {
  grid-column: span 1;
  max-width: 800px;
  margin: 0 auto;
}

h2 {
  color: #1e3a5f;
  font-weight: 700;
  margin-bottom: 2rem;
  font-size: 1.5rem;
}

h5 {
  color: #1e3a5f;
  font-weight: 600;
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}

.card {
  border: none;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  border-radius: 8px;
  overflow: hidden;
}

.card-body {
  padding: 2rem;
}

.form-label {
  font-weight: 600;
  color: #1e3a5f;
  margin-bottom: 0.5rem;
  font-size: 0.95rem;
}

.form-control,
.form-select {
  border: 1px solid #ddd;
  border-radius: 6px;
  padding: 0.75rem 1rem;
  font-size: 0.95rem;
  transition: all 0.3s ease;
}

.form-control:focus,
.form-select:focus {
  border-color: #4a90e2;
  box-shadow: 0 0 0 3px rgba(74, 144, 226, 0.1);
  outline: none;
}

.form-check {
  margin: 0.75rem 0;
}

.form-check-input {
  width: 18px;
  height: 18px;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.form-check-input:checked {
  background-color: #4a90e2;
  border-color: #4a90e2;
}

.form-check-label {
  font-weight: 500;
  color: #333;
  cursor: pointer;
  margin-left: 0.5rem;
}

.alert {
  border-radius: 6px;
  border: none;
  padding: 1rem;
  margin-bottom: 1rem;
}

.alert-danger {
  background-color: #f8d7da;
  color: #721c24;
  border-left: 4px solid #c41e3a;
}

.btn {
  border-radius: 6px;
  padding: 0.75rem 1.5rem;
  font-weight: 600;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
  font-size: 0.95rem;
}

.btn-primary {
  background-color: #4a90e2;
  color: white;
}

.btn-primary:hover {
  background-color: #357ae8;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(74, 144, 226, 0.3);
}

.link-primary {
  color: #4a90e2;
  text-decoration: none;
  font-weight: 500;
}

.link-primary:hover {
  color: #357ae8;
  text-decoration: underline;
}

.me-3 {
  margin-right: 1rem;
}

.g-3 {
  gap: 1.5rem;
}

.col-md-6 {
  flex: 0 0 calc(50% - 0.75rem);
}

.col-12 {
  flex: 0 0 100%;
}

@media (max-width: 768px) {
  .col-md-6 {
    flex: 0 0 100%;
  }
}
