.app-layout {
  display: flex;
  min-height: 100vh;
}

.main-content {
  flex: 1;
  padding: 2rem;
  background-color: #eef2f5;
  overflow-y: auto;
}

.main-content h2 {
  font-size: 1.8rem;
  color: #1e3a5f;
  margin-bottom: 2rem;
  font-weight: 700;
}

.main-content h3 {
  font-size: 1.3rem;
  color: #1e3a5f;
  margin-bottom: 1.5rem;
  font-weight: 600;
}

.main-content a {
  color: #4a90e2;
  text-decoration: none;
}

.main-content a:hover {
  color: #357ae8;
  text-decoration: underline;
}

.card {
  background: white;
  border: none;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  transition: all 0.3s ease;
}

.card:hover {
  box-shadow: 0 4px 16px rgba(0,0,0,0.15);
  transform: translateY(-2px);
}

.card-body {
  padding: 1.5rem;
}

.btn {
  border-radius: 6px;
  padding: 0.5rem 1rem;
  font-weight: 500;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
}

.btn-primary {
  background-color: #4a90e2;
  color: white;
}

.btn-primary:hover {
  background-color: #357ae8;
}

.btn-outline-secondary {
  border: 1px solid #ccc;
  color: #666;
  background-color: white;
}

.btn-outline-secondary:hover {
  border-color: #999;
  background-color: #f9f9f9;
}

.btn-danger {
  background-color: #c41e3a;
  color: white;
}

.btn-danger:hover {
  background-color: #a71830;
}

.dashboard-menu {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 2rem;
}

.dashboard-menu .btn {
  min-width: 120px;
}

.row {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1.5rem;
}

.card-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: #1e3a5f;
  margin-bottom: 0.5rem;
}

.card-text {
  color: #666;
  font-size: 0.95rem;
  margin: 0.5rem 0;
}

.text-muted-small {
  font-size: 0.85rem;
  color: #999;
}
