h2 {
  color: #1e3a5f;
  font-weight: 700;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

h4 {
  color: #1e3a5f;
  font-weight: 600;
  margin-top: 2rem;
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

h5 {
  color: #1e3a5f;
  font-weight: 600;
}

h6 {
  color: #1e3a5f;
  font-weight: 600;
}

.tab-buttons {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  border-bottom: 2px solid #eee;
  padding-bottom: 0;
}

.btn-tab {
  padding: 0.75rem 1.5rem;
  border: none;
  background: white;
  color: #666;
  cursor: pointer;
  font-weight: 500;
  font-size: 0.95rem;
  border-bottom: 3px solid transparent;
  transition: all 0.3s ease;
  margin-bottom: -2px;
}

.btn-tab:hover {
  color: #4a90e2;
}

.btn-tab.active {
  color: #4a90e2;
  border-bottom-color: #4a90e2;
}

.search-box {
  background: white;
  padding: 1.5rem;
  border-radius: 8px;
  margin-bottom: 2rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.search-label {
  display: block;
  font-weight: 500;
  color: #333;
  margin-bottom: 1rem;
  font-size: 0.95rem;
}

.search-input-group {
  display: flex;
  gap: 0.5rem;
}

.search-input {
  flex: 1;
  padding: 0.75rem 1rem;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 0.95rem;
}

.search-input:focus {
  outline: none;
  border-color: #4a90e2;
  box-shadow: 0 0 0 3px rgba(74, 144, 226, 0.1);
}

.btn-search {
  padding: 0.75rem 2rem;
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-search:hover {
  background-color: #357ae8;
}

.card {
  border: none;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  border-radius: 8px;
  margin-bottom: 1.5rem;
  transition: all 0.3s ease;
}

.card:hover {
  box-shadow: 0 4px 16px rgba(0,0,0,0.15);
  transform: translateY(-2px);
}

.card-body {
  padding: 1.5rem;
}

.course-info {
  display: flex;
  gap: 2rem;
  margin: 1rem 0;
  color: #999;
  font-size: 0.85rem;
}

.course-info-small {
  color: #999;
  font-size: 0.85rem;
  margin: 0.5rem 0;
}

.course-info-small div {
  margin: 0.25rem 0;
}

.mt-2 {
  margin-top: 0.5rem;
}

.mb-3 {
  margin-bottom: 1rem;
}

.btn {
  border-radius: 6px;
  padding: 0.6rem 1.2rem;
  font-weight: 500;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
  font-size: 0.9rem;
}

.btn-success {
  background-color: #28a745;
  color: white;
}

.btn-success:hover {
  background-color: #218838;
}

.suggested-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.suggested-card {
  position: relative;
}

.btn-add-suggested {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background-color: #4a90e2;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
}

.btn-add-suggested:hover {
  background-color: #357ae8;
  transform: scale(1.1);
}

.cart-section {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  margin-top: 2rem;
  overflow: hidden;
}

.cart-header {
  background-color: #4a90e2;
  color: white;
  padding: 1rem 1.5rem;
  font-weight: 600;
  font-size: 0.95rem;
}

.cart-body {
  padding: 1.5rem;
}

.cart-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 1.5rem;
}

.cart-table thead {
  border-bottom: 2px solid #ddd;
}

.cart-table th {
  text-align: left;
  padding: 0.75rem;
  font-weight: 600;
  color: #1e3a5f;
  font-size: 0.9rem;
}

.cart-table td {
  padding: 1rem 0.75rem;
  border-bottom: 1px solid #eee;
  color: #333;
}

.cart-table tbody tr:last-child td {
  border-bottom: none;
}

.badge {
  background-color: #6c757d;
  color: white;
  border-radius: 4px;
  padding: 0.25rem 0.6rem;
  font-size: 0.75rem;
  font-weight: 500;
}

.btn-remove {
  background-color: transparent;
  color: #c41e3a;
  border: 1px solid #c41e3a;
  padding: 0.4rem 0.8rem;
  font-size: 0.85rem;
}

.btn-remove:hover {
  background-color: #c41e3a;
  color: white;
}

.cart-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background-color: #f9f9f9;
  border-radius: 6px;
}

.btn-submit {
  background-color: #4a90e2;
  color: white;
  padding: 0.75rem 1.5rem;
}

.btn-submit:hover {
  background-color: #357ae8;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(74, 144, 226, 0.3);
}