h2 {
  color: #1e3a5f;
  font-weight: 700;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

h3 {
  color: #1e3a5f;
  font-weight: 600;
  margin-bottom: 1.5rem;
}

.course-actions {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  margin-right: .5rem;
}

/* Page specific typography & layout */
.courses-page {
  font-family: Inter, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial;
  color: #12303f;
  line-height: 1.45;
}

.course-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.course-meta {
  flex: 1 1 auto;
}

.course-title {
  color: #10293d;
  font-size: 1.08rem;
  letter-spacing: 0.2px;
}
.course-code {
  color: #6b7785;
  font-weight: 500;
  font-size: 0.95rem;
  margin-left: 6px;
}

.course-actions-right {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

/* 'View' link: different color but visible */
.course-view {
  color: #4a90e2;
  font-weight: 600;
  text-decoration: none;
  padding: 6px 8px;
  border-radius: 6px;
  transition: color 0.15s ease, background 0.15s ease;
}
.course-view:hover {
  color: #2b6fb3;
  background: rgba(74,144,226,0.08);
}

.btn-add {
  padding: 0.45rem 0.9rem;
}

.btn {
  border-radius: 6px;
  padding: 0.6rem 1.2rem;
  font-weight: 500;
  transition: all 0.3s ease;
  border: 2px solid;
  cursor: pointer;
}

.btn-outline-primary {
  border-color: #4a90e2;
  color: #4a90e2;
  background: white;
}

.btn-outline-primary:hover {
  background: #4a90e2;
  color: white;
}

.btn-outline-success {
  border-color: #28a745;
  color: #28a745;
  background: white;
}

.btn-outline-success:hover {
  background: #28a745;
  color: white;
}

.btn-primary {
  background-color: #4a90e2;
  color: white;
  border: none;
}

.btn-primary:hover {
  background-color: #357ae8;
}

.list-group {
  background: white;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  border: none;
}

.list-group-item {
  border: none;
  padding: 1.5rem;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.3s ease;
  background: white;
}

.list-group-item:last-child {
  border-bottom: none;
}

.list-group-item:hover {
  background-color: #f9f9f9;
}

.list-group-item strong {
  color: #1e3a5f;
  font-size: 1.05rem;
  display: block;
  margin-bottom: 0.5rem;
}

.text-muted-small {
  font-size: 0.85rem;
  color: #999;
  margin: 0.25rem 0;
}

.d-flex {
  display: flex;
  gap: 0.5rem;
}

.gap-2 {
  gap: 0.5rem;
}

.card {
  border: none;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  border-radius: 8px;
  margin-bottom: 1.5rem;
  transition: all 0.3s ease;
}

.card:hover {
  box-shadow: 0 4px 16px rgba(0,0,0,0.15);
  transform: translateY(-2px);
}

.card-header {
  background-color: #4a90e2;
  color: white;
  font-weight: 600;
  border: none;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  padding: 1rem 1.5rem;
}

.card-body {
  padding: 1.5rem;
}

.cart-card .card-body {
  padding: 1rem 1.25rem;
}

/* New styles to match screenshot cards */
.course-item {
  background: #fff;
  border-radius: 8px;
  padding: 0.75rem 1rem;
  margin: 0.6rem 0;
  box-shadow: 0 6px 18px rgba(16,41,61,0.06);
  align-items: center;
}

.course-left {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-right: 1rem;
}
.course-accent {
  width: 6px;
  height: 56px;
  background: linear-gradient(180deg,#4a90e2,#2a5a8f);
  border-radius: 4px;
  margin-right: 8px;
}
.course-badge {
  width: 44px;
  height: 44px;
  border-radius: 8px;
  background: #eaf3fb;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
}

.course-item .course-meta {
  flex: 1 1 auto;
}

.course-chevron {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  border-radius: 8px;
  background: transparent;
  border: none;
  color: #9aa6b2;
  text-decoration: none;
  font-size: 22px;
}
.course-chevron:hover { background: rgba(0,0,0,0.03); color: #6b7785 }

/* Tabs underline */
.tab-buttons { display: flex; gap: 0.75rem; align-items: center }
.btn-tab { background: transparent; border: none; color: #425a6b; padding: 0.4rem 0.6rem; font-weight:600 }
.btn-tab.active { position: relative; color: #12303f }
.btn-tab.active::after { content: ''; position: absolute; left: 0; right: 0; bottom: -12px; height: 3px; background: #4a90e2; width: 36px; border-radius: 4px }

.badge {
  border-radius: 4px;
  padding: 0.4rem 0.8rem;
  font-size: 0.75rem;
  font-weight: 500;
}

.text-bg-secondary {
  background-color: #6c757d !important;
  color: white !important;
}

.btn-sm {
  padding: 0.4rem 0.8rem;
  font-size: 0.85rem;
}

.btn-outline-danger {
  border: 2px solid #c41e3a;
  color: #c41e3a;
}

.btn-outline-danger:hover {
  background: #c41e3a;
  color: white;
}

.mb-4 {
  margin-bottom: 2rem;
}

hr {
  margin: 1rem 0;
  border: none;
  border-top: 1px solid #eee;
}

.justify-content-between {
  justify-content: space-between;
  align-items: center;
}