h2 {
  color: #1e3a5f;
  font-weight: 700;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

.card {
  border: none;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  border-radius: 8px;
  margin-bottom: 1.5rem;
}

.card-header {
  background-color: #4a90e2;
  color: white;
  font-weight: 600;
  border: none;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  padding: 1rem 1.5rem;
}

.card-body {
  padding: 1.5rem;
}

.confirm-summary {
  background: white;
  border: 1px solid #eee;
  border-radius: 6px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
}

.confirm-summary .row > div:first-child {
  font-weight: 600;
  color: #1e3a5f;
}

.row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-bottom: 1rem;
}

.summary-item {
  padding: 1rem;
  background-color: #f9f9f9;
  border-radius: 6px;
  border-left: 4px solid #4a90e2;
}

.summary-item label {
  color: #999;
  font-size: 0.85rem;
  display: block;
  margin-bottom: 0.25rem;
}

.summary-item .value {
  color: #1e3a5f;
  font-weight: 600;
  font-size: 1.1rem;
}

.btn {
  border-radius: 6px;
  padding: 0.75rem 1.5rem;
  font-weight: 600;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
}

.btn-primary {
  background-color: #4a90e2;
  color: white;
}

.btn-primary:hover {
  background-color: #357ae8;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(74, 144, 226, 0.3);
}

.btn-secondary {
  background-color: #6c757d;
  color: white;
}

.btn-secondary:hover {
  background-color: #5a6268;
}

.alert {
  border-radius: 6px;
  border: none;
  padding: 1rem;
  margin-bottom: 1rem;
}

.alert-success {
  background-color: #d4edda;
  color: #155724;
  border-left: 4px solid #28a745;
}

.alert-danger {
  background-color: #f8d7da;
  color: #721c24;
  border-left: 4px solid #c41e3a;
}

.text-center {
  text-align: center;
}