h2 {
  color: #1e3a5f;
  font-weight: 700;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

.card {
  border: none;
  box-shadow: 0 6px 22px rgba(16,40,70,0.08);
  border-radius: 12px;
  margin-bottom: 1.5rem;
}

.card-header {
  background-color: #758cc1;
  color: white;
  font-weight: 700;
  border: none;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  padding: 1rem 1.5rem;
}

.card-body {
  padding: 1.5rem;
}

.schedule-container {
  max-width: 980px;
  margin: 2.25rem auto;
  padding: 0 1rem;
}

.schedule-table {
  width: 100%;
  border-collapse: collapse;
  background: white;
}

.schedule-table th,
.schedule-table td {
  text-align: center;
  vertical-align: middle;
  cursor: default;
  padding: 0.85rem 1rem;
}

.schedule-table thead th {
  background-color: #2c50a3;
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.02em;
  padding: 0.9rem 1rem;
  border: none;
}

.schedule-table tbody td {
  color: #243042;
  padding: 0.85rem 0.75rem;
}

.schedule-table tbody tr + tr td {
  border-top: 1px solid #f1f5f9;
}

.schedule-table tbody tr:hover td {
  background-color: transparent;
}

.day-header {
  font-weight: 600;
  color: #1e3a5f;
}

.time-slot {
  background-color: #f9f9f9;
}

.course-slot {
  background-color: #e8f1ff;
  border-radius: 8px;
  padding: 0.5rem 0.6rem;
  display: inline-block;
  color: #184a86;
}

.course-slot:hover {
  background-color: #1e3a5f;
  color: #fff;
  transition: background-color 0.25s ease, color 0.25s ease;
}

/* Responsive: make table more mobile-friendly */
@media (max-width: 720px) {
  .schedule-table thead { display: none; }
  .schedule-table, .schedule-table tbody, .schedule-table tr, .schedule-table td { display: block; width: 100%; }
  .schedule-table tr { margin-bottom: 0.75rem; }
  .schedule-table td { text-align: left; padding: 0.75rem; border: none; background: #fff; }
  .course-slot { display: block; }
}