body {
  background: #f4f8fd;
}

/* Headings */
h2 {
  color: #111927;
  font-size: 2.1rem;
  font-weight: 800;
  margin-bottom: 1.2rem;
  letter-spacing: -0.5px;
}

.grades-card {
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 2px 12px rgba(30,60,90,0.08);
  padding: 2rem 2.2rem 1.5rem 2.2rem;
  margin: 2rem auto;
  max-width: 980px;
  border-top: 5px solid #4289e6;
}

.grades-card h3 {
  font-size: 1.4rem;
  font-weight: 700;
  color: #222b3a;
  margin-bottom: 1.2rem;
}

.grades-table {
  width: 100%;
  border-collapse: collapse;
  background: #fff;
  margin-bottom: 0;
}

.grades-table thead th {
  background: #eaf3fa;
  color: #184a86;
  font-size: 1rem;
  font-weight: 700;
  padding: 1rem 0.7rem;
  border-bottom: 2px solid #4289e6;
  text-align: left;
}

.grades-table tbody td {
  background: #fff;
  color: #222b3a;
  font-size: 1.01rem;
  padding: 0.92rem 0.7rem;
  border-bottom: 1px solid #eaf3fa;
  font-weight: 500;
}

.grades-table td.grade {
  font-weight: 700;
}
.grades-table td.grade.aplus {
  color: #1fa463;
}
.grades-table td.grade.a {
  color: #1fa463;
}
.grades-table td.grade.bplus {
  color: #4289e6;
}
.grades-table td.grade.b {
  color: #4289e6;
}

.grades-table tbody tr:last-child td {
  border-bottom: none;
}
.grades-table tbody tr:nth-child(even) td {
  background: #f6f8fa;
}
.grades-table tbody tr:nth-child(odd) td {
  background: #fff;
}


.grades-summary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5rem;
  padding-top: 1.1rem;
  border-top: 1.5px solid #eaf3fa;
  font-size: 1.08rem;
  color: #184a86;
}
.grades-summary .value {
  font-weight: 700;
  color: #184a86;
  font-size: 1.18rem;
  margin-left: 0.2rem;
}

/* spacing between rows is handled by `border-spacing` on the table */

/* Removed unused .grades-row, .subject, .dot, .badge for a cleaner table as in the screenshot */
/* Responsive */
@media (max-width: 920px) {
  .grades-grid { grid-template-columns: 1fr; }
  .summary { flex-direction: column; }
}

@media (max-width: 520px) {
  .card-body { padding: 0.85rem; }
  .grades-table thead th { display: none; }
  .grades-table, .grades-table tbody, .grades-table tr, .grades-table td { display: block; width: 100%; }
  .grades-table td { padding: 0.6rem 0.75rem; }
  .subject { gap: 0.5rem; }
}
