h2 {
  color: #1e3a5f;
  font-weight: 700;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

h3 {
  color: #1e3a5f;
  font-weight: 600;
  margin-bottom: 1.5rem;
}

.card {
  border: none;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  border-radius: 8px;
  margin-bottom: 1.5rem;
  transition: all 0.3s ease;
}

.card:hover {
  box-shadow: 0 4px 16px rgba(0,0,0,0.15);
  transform: translateY(-2px);
}

.card-header {
  background-color: #4a90e2;
  color: white;
  font-weight: 600;
  border: none;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  padding: 1rem 1.5rem;
}

.card-body {
  padding: 1.5rem;
}

.card-title {
  color: #1e3a5f;
  font-weight: 600;
  margin-bottom: 0.25rem;
  font-size: 1.05rem;
}

.text-muted {
  color: #999;
  font-size: 0.9rem;
}

.row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
}

.col {
  flex: 1;
}

.btn {
  border-radius: 6px;
  padding: 0.75rem 1.5rem;
  font-weight: 600;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
}

.btn-primary {
  background-color: #4a90e2;
  color: white;
}

.btn-primary:hover {
  background-color: #357ae8;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(74, 144, 226, 0.3);
}

.badge {
  border-radius: 4px;
  padding: 0.4rem 0.8rem;
  font-size: 0.75rem;
  font-weight: 500;
}

.text-bg-success {
  background-color: #28a745 !important;
  color: white !important;
}

.text-bg-warning {
  background-color: #ffc107 !important;
  color: #333 !important;
}

.text-bg-danger {
  background-color: #c41e3a !important;
  color: white !important;
}