.dashboard-menu .btn { min-width: 120px; }

.page-title {
  margin-bottom: 2rem;
  border-bottom: 2px solid #eee;
  padding-bottom: 1rem;
  color: #1e3a5f;
  font-weight: 700;
}

h2 {
  color: #1e3a5f;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

h3 {
  color: #1e3a5f;
  font-weight: 600;
  margin-bottom: 1.5rem;
}

.card {
  border: none;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  border-radius: 8px;
  transition: all 0.3s ease;
}

.card:hover {
  box-shadow: 0 4px 16px rgba(0,0,0,0.15);
  transform: translateY(-2px);
}

.card-body {
  padding: 1.5rem;
}

.card-title {
  color: #1e3a5f;
  font-weight: 600;
}

.btn-primary {
  background-color: #4a90e2;
  color: white;
  border: none;
}

.btn-primary:hover {
  background-color: #357ae8;
}