h2 {
  color: #1e3a5f;
  font-weight: 700;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

h3 {
  color: #1e3a5f;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

h6 {
  color: #1e3a5f;
  font-weight: 600;
}

.btn-link {
  color: #4a90e2;
  text-decoration: none;
  font-weight: 500;
  padding: 0;
  margin-bottom: 1rem;
  display: inline-block;
}

.btn-link:hover {
  color: #357ae8;
  text-decoration: underline;
}

.text-muted-small {
  font-size: 0.85rem;
  color: #999;
  margin: 0.5rem 0;
}

.card {
  border: none;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  border-radius: 8px;
  margin-bottom: 1.5rem;
  transition: all 0.3s ease;
}

.card:hover {
  box-shadow: 0 4px 16px rgba(0,0,0,0.15);
  transform: translateY(-2px);
}

.card-header {
  background-color: #4a90e2;
  color: white;
  font-weight: 600;
  border: none;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  padding: 1rem 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-body {
  padding: 1.5rem;
}

.file-list {
  background-color: #f9f9f9;
  padding: 1rem;
  border-radius: 6px;
}

.file-list .d-flex {
  display: flex;
  align-items: center;
  padding: 0.75rem;
  background: white;
  border-radius: 4px;
  margin-bottom: 0.75rem;
  border: 1px solid #eee;
}

.file-list .d-flex:last-child {
  margin-bottom: 0;
}

.badge {
  background-color: #6c757d;
  color: white;
  border-radius: 4px;
  padding: 0.25rem 0.5rem;
  font-size: 0.7rem;
  font-weight: 500;
  margin: 0 0.5rem;
}

.tools {
  background-color: #f9f9f9;
  padding: 1rem;
  border-radius: 6px;
  margin-bottom: 1rem;
}

.tools h6 {
  color: #1e3a5f;
  font-weight: 600;
  margin-bottom: 1rem;
  font-size: 0.9rem;
}

.form-check {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  margin-right: 1rem;
}

.form-check-input {
  width: 18px;
  height: 18px;
  cursor: pointer;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.form-check-input:checked {
  background-color: #4a90e2;
  border-color: #4a90e2;
}

.form-check-label {
  cursor: pointer;
  margin: 0;
}

.btn {
  border-radius: 6px;
  padding: 0.6rem 1.2rem;
  font-weight: 500;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
  font-size: 0.9rem;
}

.btn-primary {
  background-color: #4a90e2;
  color: white;
}

.btn-primary:hover {
  background-color: #357ae8;
}

.btn-outline-primary {
  border: 2px solid #4a90e2;
  color: #4a90e2;
  background: white;
}

.btn-outline-primary:hover {
  background: #4a90e2;
  color: white;
}

.btn-outline-secondary {
  border: 2px solid #6c757d;
  color: #6c757d;
  background: white;
}

.btn-outline-secondary:hover {
  background: #6c757d;
  color: white;
}

.btn-sm {
  padding: 0.4rem 0.8rem;
  font-size: 0.85rem;
}

.alert {
  border-radius: 6px;
  border: none;
  padding: 1rem;
  margin-top: 1rem;
}

.alert-info {
  background-color: #d1ecf1;
  color: #0c5460;
  border-left: 4px solid #0c5460;
}

.d-flex {
  display: flex;
}

.align-items-center {
  align-items: center;
}

.justify-content-between {
  justify-content: space-between;
}

.me-2 {
  margin-right: 0.5rem;
}

.ms-auto {
  margin-left: auto;
}

.mb-2 {
  margin-bottom: 0.5rem;
}

.mb-3 {
  margin-bottom: 1rem;
}
.tools .form-check { margin-right: 1rem; }

/* File row + actions */
.file-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  border-radius: 8px;
  padding: 12px 14px;
  margin-bottom: 12px;
  border: 1px solid #eef3f6;
}
.file-info { display:flex; align-items:center; gap:0.75rem; flex:1 }
.file-name-row { display:flex; align-items:center; gap:0.5rem }
.file-icon { width:28px; height:28px; display:flex; align-items:center; justify-content:center; border-radius:4px; flex-shrink:0 }
.pdf-icon { background:#ffebee; color:#c41e3a }
.video-icon { background:#ffebee; color:#c41e3a }
.file-details { display:flex; flex-direction:column; gap:2px }
.file-name { font-weight:600; color:#12303f; font-size:0.95rem }
.file-type { color:#99a4ab; font-size:0.8rem }

.file-row { display:flex; align-items:center; justify-content:space-between; gap:1rem; padding:0.75rem; border-radius:6px; border:1px solid #eef2f5; margin-bottom:0.75rem; background:white }
.file-info { display:flex; align-items:center; gap:0.75rem; flex:1 }
.file-name-row { display:flex; align-items:center; gap:0.5rem }
.file-actions { display:flex; gap:0.5rem; align-items:center }

.ai-menu {
  position:absolute; right:0; top:46px; width:220px; background:white; border-radius:8px; box-shadow:0 8px 24px rgba(16,41,61,0.12);
  border:1px solid #eef3f6; z-index:30; padding:8px;
}
.ai-menu-section { padding:6px 8px }
.ai-menu-section-title { padding:6px 8px; font-size:12px; color:#5b6b74; font-weight:700 }
.ai-menu-item { display:block; width:100%; text-align:left; padding:8px 10px; border:none; background:transparent; cursor:pointer; color:#163248; font-weight:600 }
.ai-menu-item:hover { background:#f4f7fb }
.ai-menu-divider { height:1px; background:#eef3f6; margin:6px 0 }

/* Enhanced icons */
.icon-svg { width:18px; height:18px; display:block; color: #163248 }

/* Download icon style: blue pill with white icon */
.icon-download { background: linear-gradient(180deg, #463ae2, #357ae8); color: white; border: none; padding-left:2px; box-shadow: 0 2px 8px rgba(7, 77, 157, 0.2) }
.icon-download:hover { transform: translateY(-2px); box-shadow: 0 8px 20px rgba(78, 61, 229, 0.3) }
.icon-download .icon-svg { color: rgb(98, 100, 224) }
/* Download icon style: light-blue background, dark on hover */
.icon-download {
  background: linear-gradient(180deg, #eaf6ff, #d6eefc); /* light blue */
  color: #357ae8; /* icon default color (dark blue) */
  border: none;
  padding-left:2px;
  box-shadow: 0 2px 6px rgba(74, 144, 226, 0.06);
}
.icon-download:hover {
  background: linear-gradient(180deg, #4a90e2, #357ae8); /* dark blue on hover */
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(42,90,143,0.18);
}
.icon-download .icon-svg { color: #357ae8 }
.icon-download:hover .icon-svg { color: white }

/* AI icon style: robot head look */
.icon-ai { background: linear-gradient(180deg, #4a90e2, #357ae8); color: white; border: none; box-shadow: 0 2px 8px rgba(74, 144, 226, 0.2) }
.icon-ai:hover { background: linear-gradient(180deg, #357ae8, #2a5a8f); box-shadow: 0 8px 20px rgba(42,90,143,0.3); transform: translateY(-2px) }
.icon-ai .icon-svg { color: rgb(96, 87, 231) }
/* AI icon style: light-blue by default, dark on hover (robot button) */
.icon-ai {
  background: linear-gradient(180deg, #eaf6ff, #d6eefc); /* light blue */
  color: #357ae8;
  border: none;
  box-shadow: 0 2px 6px rgba(74, 144, 226, 0.06);
}
.icon-ai:hover {
  background: linear-gradient(180deg, #4a90e2, #357ae8); /* dark blue on hover */
  box-shadow: 0 8px 20px rgba(42,90,143,0.18);
  transform: translateY(-2px);
}
.icon-ai .icon-svg { color: #357ae8 }
.icon-ai:hover .icon-svg { color: white }

/* Slight transitions */
.icon-btn { display:inline-flex; align-items:center; justify-content:center; width:40px; height:40px; border-radius:8px; background:#f3f7fb; border:1px solid transparent; cursor:pointer; transition: transform 120ms ease, box-shadow 160ms ease, background 160ms ease }

/* Lecture card polish */
.lecture-card .card-header { background:transparent; color:#12303f; padding:1rem; font-weight:700 }
.lecture-title { font-size:1.05rem; display:flex; align-items:center; gap:0.5rem }
.lecture-icon { width:20px; height:20px; flex-shrink:0; color:#4a90e2 }

/* make ai feedback alert subtle */
.alert-info { background:#eef7fb; color:#0a3b5d; border-left:4px solid #0a3b5d }