.sidebar {
  width: 200px;
  background: linear-gradient(135deg, #1e3a5f 0%, #2a5a8f 100%);
  color: white;
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  height: 100vh;
  box-shadow: 2px 0 10px rgba(0,0,0,0.1);
  position: sticky;
  top: 0;
}

.sidebar-header {
  padding: 0 1.5rem 2rem;
  border-bottom: 2px solid rgba(255,255,255,0.2);
  margin-bottom: 2rem;
}

.sidebar-header h3 {
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  font-weight: 700;
  color: #ffffff;
}

.sidebar-header p {
  font-size: 0.85rem;
  color: rgba(255,255,255,0.8);
  margin: 0;
}

.sidebar-nav {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 0 1rem;
}

.nav-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.5rem;
  color: rgba(255,255,255,0.8);
  text-decoration: none;
  border-radius: 8px;
  transition: all 0.3s ease;
  font-weight: 500;
  cursor: pointer;
}

.nav-item:hover {
  background-color: rgba(255,255,255,0.1);
  color: #ffffff;
  transform: translateX(5px);
}

.nav-item.active {
  background-color: rgba(255,255,255,0.25);
  color: #ffffff;
  border-left: 4px solid #ffffff;
  padding-left: calc(1.5rem - 4px);
}

.nav-item .icon {
  font-size: 1.2rem;
  display: inline-block;
}

.btn-logout {
  background-color: #c41e3a;
  color: white;
  border: none;
  padding: 1rem 1.5rem;
  margin: 1rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.95rem;
}

.btn-logout:hover {
  background-color: #a71830;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(196, 30, 58, 0.3);
}

@media (max-width: 768px) {
  .sidebar {
    width: 80px;
    padding: 1rem 0;
  }

  .sidebar-header {
    text-align: center;
    padding: 0 0.5rem 1rem;
    margin-bottom: 1rem;
  }

  .sidebar-header h3 {
    font-size: 0.9rem;
  }

  .sidebar-header p {
    display: none;
  }

  .nav-item {
    padding: 1rem;
    justify-content: center;
    gap: 0;
  }

  .nav-item span:not(.icon) {
    display: none;
  }

  .btn-logout span:not(:first-child) {
    display: none;
  }
}
